.page {
  box-sizing: border-box;
  height: 100vw;
  min-width: 360px;
  scroll-behavior: smooth;
  background: url("/images/line.svg");
  background-repeat: no-repeat;
  background-size: 100%;

  &__body {
    margin: 0 auto;
    // max-width: 1200px;
    background-color: $c-brown;

    &--with-menu {
      overflow: hidden;

      @include pcSize {
        overflow: auto;
      }
    }
  }

  &__line {
    width: 100%;
    z-index: 0;
  }

  &__menu {
    background-color: $c-green;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;

    transform: translateX(110%);
    transition: all 300ms ease-in-out;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 60px;

    @include tableSize {
      margin-bottom: 80px;
    }

    @include pcSize {
      margin-bottom: 140px;
    }

    &-title {
      padding-bottom: 24px;
      width: 100%;
    }

    &-paragraph {
      width: 100%;
    }
  }
}
