* {
  font-family: "Montserrat", sans-serif;
  color: $c-green;
}

h1 {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;

  
  @include tableSize {
    font-size: 42px;
    line-height: 52px;
  }

  @include pcSize {
    font-size: 56px;
    line-height: 68px;
  }
}

h2 {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 40px;

  @include tableSize {
    font-weight: 700;
    font-size: 28px;
  }

  @include pcSize {
    font-weight: 700;
    font-size: 32px;
  }
}

h3 {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;

  @include tableSize {
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
  }
}

p {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;

  @include tableSize {
    font-size: 20px;
    line-height: 28px;
  }
}

.txt {
  &__white {
    color: $c-white;
  }

  &__left {
    text-align: left;
  }
  
  &__right {
    text-align: right;
  }

  &__center {
    text-align: center;
  }
}
