.header {
  min-width: 370px;
  position: relative;
  margin-bottom: 30px;

  @include tableSize {
    padding: 0 24px;
    max-width: 1200px;

    margin: 0 auto 60px;

  }

  @include pcSize {
    margin-bottom: 100px;
  }
  
  &__content {
    height: max-content;
    background-color: $c-green;
  }

  &__info {
    max-width: 380px;
    padding-top: 50px;
    margin-bottom: 30px;
    text-align: left;

    @include tableSize {
      max-width: 680px;
      padding-top: 200px;
      padding-left: 60px;
      margin-bottom: 36px;
    }
  }

  &__text {
    max-width: 260px;
    padding-top: 12px;
    margin-bottom: 30px;

    @include tableSize {
      max-width: 400px;
      padding-top: 24px;
      margin-bottom: 35px;
    }
  }

  &__button {
    display: inline-block;
    background-color: $c_white;
    padding: 10px 32px;
    font-weight: 600;
  }

  &__img {
    background-image: url("../images/header_img.jfif");
    min-height: 320px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 100%;
    // overflow: hidden;
    // margin-top: 1;

    @media (min-width: 580px) {
      min-height: 440px;
      // margin-top: -100%;
    }

    @include tableSize {
      min-height: 600px;
      order: -1;
    }    

  }

  &__nav {
    width: 100%;
    display: inline-block;

    @include tableSize {
      width: 100%;
      margin: 12px 0;
    }
  }
}
