.menu {
  background-color: $c-green;

  &__nav {
    padding: 20px 20px 50px;
  }

  &__content {
    height: calc(100vh - 1px);
    display: flex;
    justify-content: center;
    align-items: center;

  }

  &__center {
    margin: 100px auto;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    color: $c-grey;
    width: max-content;
    list-style: none;
    margin-bottom: 36px;
    border-bottom: 1px solid transparent;
    font-size: 24px;

    @include tableSize {
      margin-bottom: 48px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      border-color: $c-black;
    }
  }

  &__link {
    @include tableSize {
      font-size: 32px;
    }
  }
}
