.nav {
  display: grid;
  grid-template-columns: 72px 7fr;
  gap: 20px;
  align-items: center;
  justify-items: stretch;
  width: 100%;
  margin: 0 auto;

  &__title {
    display: inline-block;
    padding-left: 12px;

    @include tableSize {
      padding-left: 0;
    }
  }

  &__menu {
    display: inline-block;
    text-align: right;
  }
  &__list {
    display: none;

    @include tableSize {
      display: block;
      text-align: right;
      list-style: none;
    }
    
  }

  &__item {
    display: inline-block;
    font-weight: 600;
    border: 1px solid transparent;
    margin-left: 36px;

    @include pcSize {
      margin-left: 50px;
    }

    &:hover {
      border-bottom: 1px solid $c-green;
    }
  }
}
