.icon {
  display: inline-block;

  width: 22px;
  height: 22px;

  background-position: center;
  background-repeat: no-repeat;

  &--menu {
    padding-right: 12px;
    background-image: url("../images/icon/menu.svg");
    @include square(20px);

    &:hover {
      background-image: url("../images/icon/menu_hover.svg");
    }

    @include tableSize {
      display: none;
    }
  }

  &--close {
    padding-right: 12px;
    background-image: url("../images/icon/menu_cross.svg");
    @include square(20px);
  }

  &--fb {
    background-image: url("../images/icon/facebook.svg");
    transition: all 300ms ease-in-out;

    &:hover {
      transform: scale(1.5);
      // background-image: url("../images/icon/fb-white.svg");
    }
  }

  &--ig {
    background-image: url("../images/icon/instargam.svg");
    transition: all 300ms ease-in-out;

    &:hover {
      transform: scale(1.5);
      // background-image: url("../images/icon/inst-white.svg");
    }
  }

  &--tw {
    background-image: url("../images/icon/twiter.svg");
    transition: all 300ms ease-in-out;

    &:hover {
      transform: scale(1.5);
      // background-image: url("../images/icon/twit-white.svg");
    }
  }
  &--yt {
    background-image: url("../images/icon/youtube.svg");
    transition: all 300ms ease-in-out;

    &:hover {
      transform: scale(1.5);
      // background-image: url("../images/icon/twit-white.svg");
    }
  }
}
