.production {
  &__description {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  &__item {
    position: relative;
    margin-bottom: -60px;
    overflow: hidden;
  }

  &__descr-posit {
    position: relative;
    border-radius: 10px;
    background-color: rgba(21, 68, 68, 0.5);
    bottom: 105px;
    height: 100px;
  }

  &__type {
    display: block;
    padding: 8px 0;
  }

  &__img {
    width: 100%;
    overflow: visible;
    background-repeat: no-repeat;
    background-size: 100%;
    border-radius: 8px;
    transition: all ease-in-out 300ms;

    &:hover {
      transform: scale(1.03);
    }
  }

  &__price {
    font-weight: 700;
    padding-bottom: 5px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
