.trust {
  text-align: center;

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    color: $c-green;

    @include tableSize {
      font-size: 32px;
      line-height: 42px;
    }
  }

  &__slider {
    overflow: hidden;
    padding-top: 24px;
    height: 24px;

    @include tableSize {
      padding-top: 48px;
      height: 48px;
    }
  }

  &__item {
    line-height: 24px;
    padding: 0 24px;
    height: 24px;
    display: inline-block;

    @include tableSize {
      line-height: 48px;
      height: 48px;
    }
  }

}
