.footer {
  background-color: $c-green;
  

  &__content {
    padding-top: 60px;
    color: $c-white;
    margin: 0 auto;
  }

  &__copyright {
    border-top: 1px solid #407980;
    padding: 12px 0;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 18px;
    width: 100%;
  
    @include tableSize {
      font-size: 16px;
      line-height: 28px;
    }
  }

  &__about {
    padding-top: 30px;

    @include tableSize {
      padding-top: 60px;
    }

    @include pcSize {
      padding-top: 100px;
    }  

  }

  &__title {
    padding-bottom: 18px;

    @include tableSize {
      padding-top: 24px;
    }
  }

  &__paragraph {
    padding-bottom: 26px;

    @include tableSize {
      padding-top: 40px;
    }
  }
  &__follow {
    padding-bottom: 30px;
  }
  &__item {
    display: inline-block;
    padding-right: 20px;

    &:last-child {
      padding-right: 0;
    }
  }

  &-left {
    text-align: center;
    
    @include tableSize {
      text-align: left;
    }
  }

  &-center {
    text-align: center;
  }

  &-right {
    text-align: center;

    @include tableSize {
      text-align: right;
    }
  }

  &__contact {
    padding-bottom: 30px;

    @include tableSize {
      padding-top: 144px;
    }

    &__item {
      padding-bottom: 12px;
    }
    
  }
}
