.grid {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  column-gap: 4px;

  @include tableSize {
    column-gap: 16px;
  }

  @include pcSize {
    column-gap: 24px;
  }
  
  &__item {

    @for $start from 1 through 10 {
      @for $end from $start through 10 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }
      }
    }

    &--table {
      @include tableSize {
        @for $start from 1 through 10 {
          @for $end from $start through 10 {
            &--#{$start}-#{$end} {
              grid-column: #{$start} / #{$end + 1};
            }
          }
        }
      }
    }

    &--pc {
      @include pcSize {
        @for $start from 1 through 10 {
          @for $end from $start through 10 {
            &--#{$start}-#{$end} {
              grid-column: #{$start} / #{$end + 1};
            }
          }
        }
      }
    }
  }
}
