.about {

  margin-bottom: 115px;

  @include pcSize {
    margin-bottom: 155px;
  }

  &__title {
    padding-bottom: 12px;

    @include tableSize {
      padding-bottom: 24px;
    }
  }

  &__description {
    padding-bottom: 30px;

    @include tableSize {
      padding-bottom: 40px;
    }
  }

  &__list {
    list-style-type: none;
  }

  &__item {
    padding-bottom: 20px;

    &-ttl {
      padding-bottom: 4px;

      @include tableSize {
        padding-bottom: 14px;
      }
    }

    &-txt {
      font-size: 10px;
      line-height: 14px;

      @include tableSize {
        font-size: 18px;
        line-height: 26px;
      }
    }
  }

  &__img {
    background-image: url("../images/aboutus_img.jfif");
    min-height: 320px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 100%;
    box-shadow: 20px 20px $c-grey;

    @include tableSize {
      box-shadow: 35px 35px $c-grey;
      order: -1;
    }
  }
}
